.autocomplete-container{position:relative}.autocomplete-container .autocomplete-search{height:100%;width:100%;background-color:#fff}.autocomplete-container .autocomplete-search:focus{box-shadow:none}.autocomplete-container .suggestions-list{z-index:998;list-style:none;padding-left:0;border-radius:0.5rem;max-height:450px;height:auto;width:100%;position:absolute;margin-top:0.5rem;list-style-type:none;background-color:#fff;box-shadow:0 15px 30px 0 rgba(0,0,0,0.11),0 5px 15px 0 rgba(0,0,0,0.08)}.autocomplete-container .suggestions-list .suggestion-item{padding:0.9rem 1rem;color:#6e6b7b;line-height:1 !important}.autocomplete-container .suggestions-list .suggestion-item a{color:#6e6b7b}.autocomplete-container .suggestions-list .suggestion-item.suggestion-title{font-size:0.85rem;color:#b9b9c3;text-transform:uppercase;letter-spacing:0.6px;margin-top:0.75rem;margin-bottom:0}.autocomplete-container .suggestions-list .suggestion-item.active,.autocomplete-container .suggestions-list .suggestion-item:hover:not(.suggestion-title-wrapper):not(.no-result){background-color:#f1f1f1;color:#6e6b7b}.autocomplete-container .suggestions-list .suggestion-item:hover:not(.suggestion-title-wrapper):not(.no-result){cursor:pointer}.autocomplete-container .suggestions-list.open-up{top:auto;bottom:100%;margin-top:0;margin-bottom:0.5rem}.dark-layout .autocomplete-container .suggestions-list{background-color:#283046}.dark-layout .autocomplete-container .suggestions-list .suggestion-item{color:#b4b7bd !important}.dark-layout .autocomplete-container .suggestions-list .suggestion-item.suggestion-title-wrapper{color:#676d7d}.dark-layout .autocomplete-container .suggestions-list .suggestion-item:not(.suggestion-title-wrapper).active,.dark-layout .autocomplete-container .suggestions-list .suggestion-item:not(.suggestion-title-wrapper):hover{background-color:#161d31 !important}
